	<div class="w foodwrapper">
	      <div class="sequence w">
	      		<span @click='all'></span>
		        <span @click='onekilo'>1km</span>
		        <span @click='twokilo'>1-3km</span>
		        <span @click='threekilo'>5km+</span>
     	 </div>
		<div class="foodcard" v-for ='(item,index) in foodlist'  @click='showdetail(item)' @mouseover='showcard(index)' @mouseout='hidecard(index)'>
			<div class="foodleft">
				<div class="foodimg">
					<img src="../assets/gakki.jpg">
					<div class="foodbrand"></div>
				</div>
				<div class="fooddetail" ref='dialogcard'>
					<minicard :food='item'></minicard>
				</div>
				<span class="deliverytime">{{item.order_lead_time}}</span>
			</div>
			<div class="foodright">
				<div class="foodname">{{item.name}}</div>
				<div class="foodranting">
					<span></span><span>{{item.recent_order_num}}</span>
				</div>
				<div class="fooddeverycost">{{item.float_delivery_fee}}</div>
				<div class="foodsupport">
