				}else{
					extension = 'png'
				}
				newdata.splice(1,0,'/')
				newdata.splice(4,0,'/')
				data = newdata.join('')
				return `//fuss10.elemecdn.com/${data}.${extension}?imageMogr2/thumbnail/70x70/format/webp/quality/85`
			},
